@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position:absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  justify-content: center;
  background: #47E5BC;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 100%;
}

#calculator{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 400px;
  width: 350px;
  border-radius: 58px;
}

  #display-wrapper{
    font-family: 'Orbitron', sans-serif;
    height: 120px;
    width: 50px;
    border-radius: 50px 50px 0 0;
    width: 100%;
    padding: 0px;
    margin: 0;
    border: solid 3px;
    border-bottom: 0px;
    background: #A5A58D;
    display: flex;
    align-items: center;
  }
  #display{
    overflow-wrap: break-word;
    display:block;
    font-size: 20px;
    max-height: 100px;
    width: 100%;
  }

  #button-wrapper{
    display: flex;
    height: 100%;
  }

  #number-pad{
    width: 75%;
    height: 100%;
  }

    .num-button{
      width: calc(100%/3);
      height: 25%;
      outline: none;
      font-size: 30px;
      border: solid 1px;
      background: #DDBEA9;
    }

    #zero{
      width: 100%;
      border-radius: 0 0 0 55px;
      border-left: solid 3px;
      border-bottom: solid 3px;
      box-shadow: 18px 13px 13px -6px;
    }
    #one, #four, #seven{
      border-left: solid 3px;
    }
  #operator-pad{
    width: 25%;
    height: 100%;
  }

    .op-button{
      width: 100%;
      height: calc(75%/7);
      font-size: 23px;
      outline: none;
      border: solid 1px;
      border-right: solid 3px;
      background: #FFF1E6;
      padding-top: 0;
    }
    #equals{
      height: 25%;
      padding-top: 0;
      border-radius: 0 0 55px 0;
      border-bottom: solid 3px;
      background: #586994;
      box-shadow: 0 13px 13px -6px;
    }
    #clear{
      background: #B3001B;
    }
    
@media only screen and (max-width: 1000px){
  #heading {
    font-size: 25px;
  }
  .container{
    font-size: 10px;
  }
}
@media only screen and (max-width: 700px){
  .App{
    flex-direction: column;
  }
    #calculator{
      width: 300px;
      height: 400px;
    }
}
#history{
    display: flex;
    flex-direction: column;
    top: calc(50% + 400px/2 + 50px);
    width: 25%;
    margin-left: 5%;
}

.container{
  border: solid;
  margin-bottom: 20px;
  border-radius: 30px;
  background: #A5A58D;
  font-family: 'Orbitron', sans-serif;
  font-size: 13px;
}

  #value0{
      -webkit-animation-name: slide;
              animation-name: slide;
      -webkit-animation-duration: 0.5s;
              animation-duration: 0.5s;
  }

  #heading{
    text-align: left;
  }

@-webkit-keyframes slide {
  from{margin-left:100%;}
  to {margin-left:0;}
}

@keyframes slide {
  from{margin-left:100%;}
  to {margin-left:0;}
}

@media only screen and (max-width: 700px){
  #history{
    margin-left: 0;
    width: 50%;
    position: absolute;
  }
}
